@font-face {
  font-family: "Montserrat";
  font-style: normal;
  src: url(../fonts/Montserrat-Medium.ttf);
  font-weight: 300px;
}
@font-face {
  font-family: "Mont-li";
  font-style: normal;
  font-weight: 100;
  src: url(../fonts/Montserrat-ExtraLight.ttf);
}

@import 'normalize.css';
@import '_base.scss';
$mbg: #f5f6f8;
$color-menu: #232323;
$menu-height: 42px;
$black: #000;
$blue: #26e6e6;
$white: #fff;
$fontMon: 'Montserrat', sans-serif;
$fontLight: 'Mont-li', sans-serif; 
body{
  max-width: 100%;
  font-family: $fontMon;
  background: $mbg;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $black;
  padding-bottom: 100px;
}
.menu{
  max-width: 100vw;
  position: fixed;
  .container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .navigation{
      max-width: 80vw;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      .menu-items{
        display: flex;
        align-items: center;
        .menu-item{
          padding: 15px;
          opacity: 0.5;
          color: $color-menu;
          font-size: 12px;
          text-transform: uppercase;
          letter-spacing: 0.6px;
          transition: $hover_transition;
        }
        .menu-item:hover{
          opacity: 1;
        }
        .active{
          opacity: 1;
        }
        .start-project{
          cursor: pointer;
          border: none;
          padding: 15px;
          border-radius: 2px;
          background: $black;
          color: $white;
          font-size: 12px;
          text-transform: uppercase;
          transition: $hover_transition;
        }
        .start-project:hover{
          background: $white;
          color: $black;
          transition: $hover_transition;
        }
      }
    }
    .menu-toggle{
      display: none;
      position: relative;
      margin-left: 20px;
      width: 20px;
      height: 1px;
      border-top: 1px solid $black;
    }
    .menu-toggle::before{
      content: "";
      width: 20px;
      position: absolute;
      bottom: 6px;
      border-top: 1px solid $black;
    }    
    .menu-toggle::after{
      content: "";
      width: 20px;
      position: absolute;
      top: 6px;
      border-top: 1px solid $black;
    }    
    .logo{
      cursor: pointer;
    }
  }

}

.main-top{
  background-image: url(/src/img/main_bg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  .top-text{
    margin: 0 auto;
    margin-top: 30vh;
    display: block;
    text-transform: uppercase;
    .top-title{
      margin: 0; 
      width: 50%;
      font-family: $fontLight;
      font-size: 70px;
      text-transform: uppercase;
      letter-spacing: 5px;
    }
  }
}
.main-bottom{
  min-height: 20vh;
  .top-items{
    display: flex;
    justify-content: space-between;
    .top-item{
      margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 10px 20px;
      text-align: center;
      min-height: 250px;
      background: red;
        .img{
          margin: 0;
        }
        .item-title{
          margin: 0;
          text-transform: uppercase;
          color: $color-menu;
          font-size: 14px;
          letter-spacing: 0.7px;
          display: flex;
          align-items: center;
          .new{
            margin-left: 5px;
            background: $blue;
            color:$white;
            padding: 5px;
            border-radius: 12px;
            font-size: 10px;
            text-transform: uppercase;
          }
        }
        .item-text{
          margin: 0;
          color: $color-menu;
          opacity: 0.5;
          font-size: 14px;
        }
        .item-link{
          margin: 0;
          text-transform: uppercase;
          font-size: 12px;
          color: $color-menu;
        }
    }
  }

}


